.form {
  width: 60%;
  height: 100vh;
  display: flex;
  margin: 0 auto;
  align-items: center;
}

.form__container {
  width: 100%;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form__container__input {
  background-color: yellowgreen;
  width: 60%;
  height: 10vh;
  transform: skew(170deg);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.form__container__input__input {
  display: inline-block;
  border: none;
  background: transparent;
  width: 80%;
  padding: 10px;
  transform: skew(10deg);
  font-size: 20px;
  outline: none;
  transition: 0.3s;
}

.form__container__input__input:active {
  border: none;
  list-style: none;
}

.form__container__input__input:focus {
  border: none;
  list-style: none;
  outline: none;
  border-bottom: 1px solid #1a73a8;
}

.form__container__input__input:focus ~ .form__container__input__label,
.form__container__input__input:not(:placeholder-shown) ~ .form__container__input__label {
  top: 20px;
  font-size: 12px;
  color: #e0e0e0;
}

.form__container__input__label {
  position: absolute;
  top: 35px;
  left: 65px;
  z-index: -1;
  color: #9e9e9e;
  transition: 0.3s;
  transform: skew(10deg);
  font-size: 20px;
  font-family: sans-serif;
}

.form__container__button {
  background-color: chocolate;
  width: 40%;
  height: 10vh;
  transform: skew(170deg);
  border: none;
  outline: none;
  transition: 0.3s;
  cursor: pointer;
}

.form__container__button :focus {
  border: none;
  outline: none;
}

.form__container__button :hover {
  transform: scale(1.2);
}

.form__container__button :active {
  border: none;
  outline: none;
}

.form__container__button__span {
  display: inline-block;
  transform: skew(10deg);
  font-size: 20px;
  transition: 0.3s;
}

.error {
  border: 3px solid red;
  padding: 15px;
  border-radius: 15px;
}

.form__container__input__input:not(:placeholder-shown) ~ .form__container__input__label {
  top: 20px;
  font-size: 12px;
  color: #e0e0e0;
}

@media all and (max-width: 700px) {
  .form {
    width: 90%;
  }
  .form__container__input {
    transform: skew(0deg);
  }
  .form__container__input__input {
    transform: skew(0deg);
    font-size: 12px;
  }
  .form__container__input__label {
    font-size: 12px;
    left: 30px;
    top: 18px;
    transform: skew(0);
  }
  .form__container__button {
    transform: skew(0deg);
  }
  .form__container__button__span {
    transform: skew(0deg);
    font-size: 12px;
  }
}

.form__container__input__input:focus ~ .form__container__input__label {
  display: none;
}
